html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;color:#434343;color:var(--text)}:root{--text: #434343;--text-second: #262626;--text-third: #1f1f1f;--text-paragraphe: #8c8c8c;--text-icon: #595959;--dark-red: #d4302a;--delimiter-gray: #d9d9d9;--blue: #0468ff;--white: #ffffff;--bg-gray: #f0f0f0;--scale-small: scale(1.05);--scale-medium: scale(1.1);--transition-3: 0.3s;--max-width: 1136px;--max-width-actual: calc(var(--max-width) + ((var(--spacer) * 9) * 2));--spacer: 8px}@media screen and (max-width: 995px){:root{--spacer: 6px}}@media screen and (max-width: 769px){:root{--spacer: 4px}}.content{max-width:calc(1136px + ((8px * 9) * 2));max-width:var(--max-width-actual);width:100%;margin:0 auto;padding:0 calc(8px*9);padding:0 calc(var(--spacer)*9)}.responsive-image{max-width:100%;height:auto}.block-center{display:flex;align-items:center;justify-content:center}*{box-sizing:border-box}*:before{box-sizing:border-box}*:after{box-sizing:border-box}*:focus{outline:none !important}body,html{margin:0;padding:0;width:100%;min-height:100vh;scroll-behavior:smooth}.lock{overflow:hidden}
